.wrapper  {
    background-color: blue;
    text-align: center;
    height: 75vh;
    padding: 5em 0 0 0;
}
.body  {
    padding-bottom: 2%;
    background-color: blue
}
.button  {
    font-family: 'Contrail One';
    font-size: 24px;
    color: orange;
    background-color: black;
    height: auto;
    width: 15%;
    padding: 0 1%;
}

.font  {
    text-align: center;
    color: blue;
    font-weight: bold;
    font-size: 24px;
    font-family: 'Contrail One';
    text-shadow: 1px 1px orange;
    height: 55%
}
.inst  {
    text-align: left;
    font-family: 'Contrail One';
    font-size: 24px;
    color: orange;
    padding-top: 1%
}
.inst-body {
    text-align: left;
    margin: 0;
    padding: 0;
    width: 100%;
}
.headline  {
    font-size: 42px;
    color: orange;
    text-align: center;
    font-family: 'Contrail One';
    padding-top: 1%
}
.diff  {
    font-family: 'Contrail One';
    font-size: 32px;
    color: orange;
    background-color: black;
    height: 50px;
    width: 15%;
    text-align: center
}

@media screen and (max-width: 500px) {
    /* .wrapper {
        height: 100vh;
    } */
    .inst {
        font-size: 16px;
        margin: 0 5% 5% 5%;
    }
    .button,
    .diff {
        width: 60%;
        height: auto;
    }
}



